<style>
  /*权限选项宽度定义*/
  label.ivu-checkbox-wrapper.ivu-checkbox-group-item {
    width: 110px;
  }
</style>
<template>
  <!--组织机构->角色管理-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">角色管理</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel">
      <Input v-model="searchParam" class="grey" placeholder="名称" @on-enter="getRoleList(1)" style="width:300px"/>
      <Button @click="getRoleList(1)" class="grey" style="margin-left:5px;width:80px;">搜索</Button>
      <Button v-if="editable===true" class="grey" style="margin-left:5px;width:80px;float: right" @click="openNewRole()" >添加</Button>
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns" border :data="roles" class="auto-height-table grey" style="width:100%">
      </Table>
      <!--分页位置-->
      <div style="margin-top: 10px;">
        <div style="float: right;">
          <Page @on-change="getRoleList" class="default" :total="pageTotal" :current="pageNum" :pageSize="pageSize" :show-total="true"></Page>
        </div>
      </div>
    </div>

    <!--添加/修改角色-->
    <Modal v-model="modal" class="default" :title="title" width="800" :mask-closable="false" @on-cancel="cancel">
      <Form ref="newRoleFrom":model="newRole" style="padding: 5px;" :label-width="100" :rules="newRoleValidate">
        <Form-item prop="roleName" label="角色名称：" style="padding:0px;">
          <Input :disabled="newRole.adminInd=='Y' || this.editable===false ? true : false" placeholder="输入角色名称"  v-model="newRole.roleName" class="grey" style="width:70%" />
        </Form-item>
        <Form-item prop="description" label="角色描述：" style="padding:0px;">
          <Input :disabled="newRole.adminInd=='Y' || this.editable===false ? true : false" placeholder="输入角色描述" v-model="newRole.description" class="grey" style="width:70%" />
        </Form-item>
        <label style="font-size: 14px;margin-left: 15px;">选择权限：</label>
        <div style="border: 1px solid; width:740px; height:200px; overflow:auto;padding:5px 5px 5px 30px;margin-left: 10px;">
          <FormItem :label-width="10" >
            <Row>
              <div v-if="this.editable === false">
                <CheckboxGroup  v-for="auth in baseRoleList" v-model="newRole.auths">
                  <iCol span="6">
                    <Checkbox :label="auth.authCode" :disabled=true>
                      <span>{{auth.authName}}</span>
                    </Checkbox>
                  </iCol>
                </CheckboxGroup>
              </div>
              <div v-else>
                <CheckboxGroup v-for="auth in baseRoleList" v-model="newRole.auths">
                  <iCol span="6">
                    <Checkbox :label="auth.authCode" :disabled="newRole.adminInd === 'Y' ? true : false">
                      <span>{{auth.authName}}</span>
                    </Checkbox>
                  </iCol>
                </CheckboxGroup>
              </div>
            </Row>
          </FormItem>
        </div>
      </Form>
      <div slot="footer">
        <Button class="grey"  style="margin-right: 5px; width: 80px;" @click="cancel">取消</Button>
        <Button v-if="title=='添加新角色' && editable" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="addRole('newRoleFrom')">保存</Button>
        <Button v-if="title=='角色信息修改' && newRole.adminInd!='Y' && editable" type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="updRole('newRoleFrom')">保存</Button>
      </div>
    </Modal>
    <!-- 删除之前警告信息-->
    <Modal v-model="delModal" class="default" :title="'真的要删除吗'" width="600" :mask-closable="false">
      <div slot="footer">
        <Button class="grey"  style="margin-right: 5px; width: 80px;" @click="delModal= false">取消</Button>
        <Button type="primary"class="grey" style="margin-left: 15px; width: 80px;" @click="delRole">删除</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import server from "../../../config/api";
  import Vue from 'vue';
  import niceScroll from 'jquery.nicescroll';

  export default {
    data(){
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        role:'',
        delModal:false,
        pageSize:20,
        pageNum: undefined,
        organizationId:'',
        newRole: {
          roleName: '',
          pid: '',
          orgId: '',
          description: '',
          auths: []
        },
        roles:[],
        pageTotal: undefined,
        modal:false,
        searchParam:'',
        title:'',
        baseRoleList:undefined,
        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width:50
          },
          {
            title: '角色名称',
            key: 'roleName',
            align: 'center',
            render:(h,params)=>{
              return h('div',[
                h('a',{
                  on:{
                    click:()=>{
                      this.openRole(params.row);
                    }
                  }
                },params.row.roleName)
              ]);
            }
          },
          {
            title: '描述',
            key: 'description',
            align: 'center'
          },
          {
            title: '操作',
            align: 'center',
            width:80,
            render:(h,params)=>{
              if(params.row.adminInd!='Y' && this.editable){
                return h('div', [
                  h('i-button',{
                    props:{
                      type:'text',
                      size:'small'
                    },
                    style:{
                      color:'#f9caca'
                    },
                    on:{
                      click:()=>{
                        this.openDelModal(params.row.roleId)
                      }
                    }
                  },'删除')
                ]);
              }
            }
          }
        ],
        newRoleValidate: {
          roleName: [
            { required: true, message: '角色名称不能为空', trigger: 'blur' }
          ]
        }
      };
    },
    computed: {
      editable: function () {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.AuthMgmt);
      }
    },
    methods: {
      createRoleTable:function () {
        //获取基础权限列表
        this.getBasicsRole();
        //获取当前机构拥有哪些定义好的权限
        this.getRoleList(1);
      },
      getBasicsRole:function () {
        this.$http.get(server.api.organization.role.getBasicsRole + "?level="+"ORG" + "&pid="+localStorage.default_pid+"&orgId="+ this.organizationId).then((response)=>{
          this.baseRoleList= response.body.data;
        }, (error) => {
          this.$Message.error('基础角色获取失败!');
        });
      },
      getRoleList:function (pageNum) {
        this.$http.get(server.api.organization.role.getRoleList.replace('{orgId}', this.organizationId)+"?roleName="+this.searchParam+"&pageSize="+this.pageSize+"&pageNum="+pageNum).then((response)=>{
          this.roles= response.body.data.rows;
          this.pageTotal = response.body.data.totalCount;
          this.pageNum = pageNum;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        }, (error) => {
          this.$Message.error('机构角色列表获取失败!');
        });
      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      openRole:function (role) {
        this.title='角色信息修改';
        this.newRole = JSON.parse(JSON.stringify(role));
        this.modal=true;
      },

      openNewRole:function () {
        this.title="添加新角色";
        this.newRole={};
        this.modal=true;
      },
      cancel:function () {
        this.$refs['newRoleFrom'].resetFields();
        this.modal=false;
      },
      delRole:function () {
        if(this.roles.length==1 && this.pageNum !=1){
          this.pageNum=this.pageNum-1;
        }
        this.$http.delete(server.api.organization.role.delRole.replace('{roleId}', this.role)).then((response)=>{
          this.getRoleList(this.pageNum);
          this.delModal=false;
          this.$Message.success('角色删除成功!');
          }, (error) => {
          this.$Message.error('角色删除失败!');
        });
      },
      addRole:function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.newRole.auths===undefined){
              this.$Message.error("请选择权限");
              return;
            }
            this.newRole.pid=localStorage.default_pid;
            this.newRole.orgId=this.organizationId;
            this.$http.post(server.api.organization.role.addRole,this.newRole).then((response)=>{
              this.getRoleList(this.pageNum);
              this.modal=false;
              this.$Message.success('添加成功!');
            },(error) => {
              this.$Message.error('添加失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      updRole:function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          if(this.newRole.auths===undefined || this.newRole.auths.length===0){
            this.$Message.error("请选择权限");
            return;
          }
          this.newRole.pid=localStorage.default_pid;
          this.newRole.orgId=this.organizationId;
          this.$http.put(server.api.organization.role.updRole,this.newRole).then((response)=>{
            this.getRoleList(this.pageNum);
            this.modal=false;
            this.$Message.success('修改成功!');
          },(error) => {
            this.$Message.error('修改失败！');
          })
        } else {
          this.$Message.error('表单验证失败!');
        }
      })
      },
      openDelModal:function (role) {
        this.role=role;
        this.delModal=true;
      }
    },
    mounted(){
      let page = this.topYunSession('route_list');
      if(page === null || page.orgId === undefined) {
        // 根节点
        this.organizationId = localStorage.current_org_id;
      } else {
        this.organizationId = page.orgId;
      }
      this.createRoleTable();
      Vue.nextTick(() => {
        $(".auto-height-table .ivu-table-body").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
    }
  }
</script>



